import http from '../../common/http.js'
export default {
	data() {
		return {
			phoneNum: '',
			password: '',
			code: '',
			tips: '',
			// refCode: null,
			seconds: 60,
			error: '',
			disabledGetCode: false
		}
	},
	onLoad: function() {

		this.allRequest()
	},
	onShow: function() {
		uni.getStorage({
			key: 'userInfo',
			success: function(res) {
				console.log(res.data);
			},
			fail: function(e) {
				console.log(e)
			}
		});
	},
	onPullDownRefresh: function() {

		// uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {

		},
		codeChange: function(text) {
			this.tips = text;
		},
		end: function() {
			this.disabledGetCode = false
		},
		start: function() {
			this.disabledGetCode = true
		},
		//执行注册
		doSignin: function() {
			uni.showLoading({
				title:'请稍后'
			})
			let _this = this
			if (!_this.validation()) {
				_this.$refs.uTips.show({
					title: _this.error,
					type: 'warning',
					duration: '2300'
				})
				return false
			}
			let opts = {
				url: 'AppWx/register',
				method: 'post'
			};
			let param = {
				username: _this.phoneNum,
				password: _this.password,
				mobile: _this.phoneNum,
				code: _this.code,
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				uni.hideLoading()
				// return
				if (res.data.code == 200) {
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel:false,
						success: function(res) {
							if (res.confirm) {
								uni.navigateBack({
									delta:1
								})
							} 
						}
					});

				} else {
					_this.$refs.uTips.show({
						title: res.data.msg,
						type: 'error',
						duration: '2300'
					})
				}

			}, error => {
				console.log(error);
				uni.hideLoading()
			})
		},
		//获取验证码
		getCode: function() {
			let _this = this
			let reg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(17[0-9]{1}))+\d{8})$/;
			if (!reg.test(_this.phoneNum)) {
				_this.$refs.uTips.show({
					title: '请输入您的手机号',
					type: 'warning',
					duration: '2300'
				})
				return false;
			}
			let opts = {
				url: 'login/sendsms',
				method: 'post'
			};
			let param = {
				tel: _this.phoneNum,
			};
			http.httpTokenRequest(opts, param).then(res => {
				uni.showLoading({})
				if (res.data.code == 200) {
					setTimeout(() => {
						uni.hideLoading();
						_this.$refs.uTips.show({
							title: res.data.msg,
							type: 'success',
							duration: '2300'
						})
						this.$refs.uCode.start(); // 通知验证码组件内部开始倒计时

					}, 2000);

				} else {
					_this.$refs.uTips.show({
						title: res.data.msg,
						type: 'error',
						duration: '2300'
					})
				}
				console.log(res.data);
			}, error => {
				console.log(error);
			})

		},
		// 表单验证
		validation: function(v) {
			let _this = this
			let reg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(17[0-9]{1}))+\d{8})$/;
			if (!reg.test(_this.phoneNum)) {
				this.error = '请填写正确的手机号';
				return false;
			}
			if (_this.password === '') {
				this.error = '请输入您的密码';
				return false;
			}
			if (_this.code === '') {
				this.error = '请输入您的验证码';
				return false;
			}
			return true;
		}
	}
}
